html,body {
  background-color: #21232a;
  color: white;
  font-size: 1.3rem;
  font-family: Helvetica, Arial, sans-serif;
  padding-top: 20px;
}

input{
  color: gray;
  width: 50%;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p{
  font-size: small;
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 190px;
}